          {positions:'top:77.9%;left:86.7%',title:"北京第二外国语学院",way:false},
          {positions:'top:23.9%;left:93%',title:"北京服装学院",way:true},
          {positions:'top:8.9%;left:72.7%',title:"北京工业大学",way:true},
          {positions:'top:34.9%;left:84.7%',title:"北京中医大学",way:false},
          {positions:'top:67.9%;left:49%',title:"北京青年政治学院",way:false},
          {positions:'top:40.9%;left:25.7%',title:"北京经济贸易大学",way:false},
          {positions:'top:-3.1%;left:50%',title:"北京联合大学",way:true},
        ],
        [
          {positions:'top:60.4%;left:26.9%',title:"北京大学",way:true},
          {positions:'top:2.9%;left:24.4%',title:"北京农业大学",way:true},
          {positions:'top:1.9%;left:60%',title:"北京交通大学",way:true},
          {positions:'top:78.9%;left:11.7%',title:"北京理工大学",way:false},
