      areaList:"",
      albumpic_big:require("../../static/img/one.png"), 
      region:"朝阳区",
    }
  },
  methods:{
    tab(event,num,areaNum){
     if ($(event.target).parent()[0].className === "headtab"){
      var _this = $(event.target).parent()[0];
      $(event.target).parent().css("background","#EFDCBB").siblings().css("background","#fff");
      $(_this).children(".area").css("color","#2C2C2C");
      $(_this).siblings().children(".area").css("color","#2D2D2D");
      $(_this).children(".school").css("color","#656565");
      $(_this).siblings().children(".school").css("color","#D1D1D1");
      $(_this).children("img").show();
      $(_this).siblings().children("img").hide();
      this.region = $(_this).children(".area").html();
      // $(".cont-one").css("background-image","url(../../static/img/"+ num + ".png)");
