      this.areaList = allArea[areaNum];
     }      
    }, 
    jump:function(t){
      //  /home/part1  //home/part2
      this.$router.push({path:"/details",query:{title:t,region:this.region}})
    }
  },
  created:function(){
     this.areaList = allArea[0];
    // this.$router.push({path:"/home"})
  
  },
  mounted(){
    
  }
