      border: none;
      height: 0.42666666rem;
      background: #FF6D6D;
      border-radius: 0.16rem;
      ont-family: PingFangSC-Medium;
      font-size: 0.2666667rem;
      color: #FFFFFF;
    }
    .tp p:nth-child(2),.bt p:nth-child(1){
