<template>
  <div class="details" style="height: 100%;">
     <div class="order">  
       <span style="white-space:pre;"></span><span class="line" style="vertical-align: 6%;margin-right: 3%"></span>  
       <span style="white-space:pre;"></span><span class="txt">{{detailsRegion}}</span>  
       <span style="white-space:pre;"></span><span class="line" style="vertical-align: 6%;margin-left: 3%"></span>  
     </div> 
     <div class="school">
        <span>{{school}}</span>
     </div>
     <div class="smallTtle">咖啡机</div>
     <div class="saleNum">
        <div style="border-bottom: 1px solid #DFDFDF;"><span>今日销量</span><span>500杯</span></div>
        <div><span>月销量</span><span>50000杯</span></div>
     </div>
     <div class="smallTtle">购买量</div>
     <div class="manNum">
        <div style="border-bottom: 1px solid #DFDFDF;"><span>限购买份额</span><span>100份</span></div>
        <div style="border-bottom: 1px solid #DFDFDF;"><span>已购买份额</span><span>50份</span></div>
        <div style="border-bottom: 1px solid #DFDFDF;"><span>剩余份额</span><span>50份</span></div>
        <div style="border-bottom: 1px solid #DFDFDF;"><span>购买份额</span><span><div class="add"><span><img src="../../static/img/del.png" alt="" @click="del()"></span> <span>{{productNum}}</span> <span><img src="../../static/img/add.png" alt=""  @click="add()"></span></div></span></div>
        <div><span>购买日期</span><span>2018年2月23日</span></div>
        <div><span>收益日期</span><span>2018年3月23日</span></div>
     </div>
     <div class="prompt">
          <span>每人限购十个名额哦！<br>可在不同区域的大学分别购买</span>
     </div>
     <div class="buyBtn">
      <router-link :to="{path: 'payment', query: { topay:school}}">
        <button style="-webkit-tap-highlight-color:transparent;">我要购买</button>
      </router-link>
     </div>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  name: 'details',
  data () {
    return {
      school:"",
      detailsRegion:"",
      productNum:1
    }
  },
  methods:{
    add(){
      this.productNum++;
    },
    del(){
      this.productNum--;
      if (this.productNum<=1) {
        this.productNum = 1;
      }
    }
  },
  created:function(){
    // 接受home的传的参数
    this.school = this.$route.query.title;
    this.detailsRegion = this.$route.query.region;
  },
  mounted:function(){
    
  },
 destroyed: function () {
    
 }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!-- scoped说明样式只能在当前组件用 -->
<style scoped>
 .order {  
     height: 1.2533335rem;  
     line-height: 1.2533335rem;  
     text-align: center;  
     background: #fff;
  }  
   .order .line {  
     display: inline-block;  
     width: 14.7%;  
     border-top: 1px solid #7B7D5C; ;  
  }  
  .order .txt {  
     vertical-align: middle;  
     font-size: 0.4rem;
     font-family: PingFangSC-Medium;
    color: #7B7D5C;
  }
  .school{
    height: 1.2533335rem;
    background: #fff;
  }
  .school span{
    display: inline-block;
    /*width: 40.7%;*/
    padding: 0 8.4%;
    height: 0.72rem;
    line-height: 0.72rem;
    border-radius: 1.3333334rem;
    background: #EFDCBB;
    font-family: PingFangSC-Medium;
    font-size: 0.4rem;
    color: #7B7D5C;
  }
  .smallTtle{
    width: 93%;
    margin: 0 auto;
    height: 0.8794667rem;
    line-height: 0.8794667rem;
    text-align: left;
    font-family: PingFangSC-Medium;
    font-size: 0.2666667rem;
    color: #7B7D5C;
  }
  .saleNum{
    height: 2.506667rem;
    background: #fff;
    /*margin-bottom: 0.2668rem;*/
  }  
  .saleNum>div{
    height: 1.2533335rem;
  }
  .saleNum>div>span,.manNum>div>span{
    display: inline-block;
    font-family: PingFangSC-Regular;
    font-size: 0.32rem;
    color: #613F05;
    line-height: 1.2533335rem;
  }
  .saleNum>div>span:nth-child(1),.manNum>div>span:nth-child(1){
    width: 46.5%;
    text-align: left;
  }
  .saleNum>div>span:nth-child(2),.manNum>div>span:nth-child(2){
    width: 46.5%;
    text-align: right;
  }
  .manNum{
    /*height: 4.3933067rem;*/
    background: #fff;
  }

  .add>span{
    font-family: PingFangSC-Regular;
    font-size: 0.32rem;
    color: #613F05;
  }
   .add>span:first-child,.add>span:last-child {
      display: inline-block;
      width: 0.38666667rem;
      height:  0.38666667rem;     
   }
  .add>span:first-child img,.add>span:last-child img{
    width: 100%;
  }
  .prompt{
    width: 100%;
    display: table;
    height: 1.84rem;
    text-align: center;
    font-family: PingFangSC-Regular;
    font-size:0.32rem;
    color: #7C7C7C;
  }
  .prompt span{
    display: table-cell;
    vertical-align:middle;
  }
  .buyBtn{
    width: 100%;
    height: 1.08rem;
    text-align: center;
  }
  .buyBtn button{
    width: 65.9%;
    height: 100%;
    background: #EFDCBB;
    border-radius: 1.333333rem;
    outline: none;
    border: none;
    font-family: PingFangSC-Regular;
    font-size: 0.4rem;
    color: #613F05;
  }
</style>



