<template>
  <div class="payment" style="height: 100%;">
      <div class="page">
        <div class="content">
               <div class="order">  
                 <span style="white-space:pre;"></span><span class="line" style="vertical-align: 6%;margin-right: 3%"></span>  
                 <span style="white-space:pre;"></span><span class="txt">朝阳区</span>  
                 <span style="white-space:pre;"></span><span class="line" style="vertical-align: 6%;margin-left: 3%"></span>  
               </div> 
               <div class="saleNum">
                  <div style="border-bottom: 1px solid #DFDFDF;"><span>{{paySchool}}咖啡机</span><span>一份</span></div>
                  <div style="border-bottom: 1px solid #DFDFDF;"><span>金额</span><span>688元</span></div>
                  <div style="border-bottom: 1px solid #DFDFDF;"><span>月收益</span><span>约150元</span></div>
                  <div style="border-bottom: 1px solid #DFDFDF;"><span>购买日期</span><span>2018年2月23日</span></div>
                  <div style="border-bottom: 1px solid #DFDFDF;"><span>收益日期</span><span>2018年3月23日</span></div>
                  <div><span>月收入</span><span>100元起</span></div>
               </div>
               <div class="pay">
                  <div style="border-bottom: 1px solid #DFDFDF;"><span><img src="../../static/img/weipay.png" alt="" > 微信支付</span><span><img src="../../static/img/noxuan.png" alt="" @click="weixinPay($event)"></span></div>
                  <div><span><img src="../../static/img/bankpay.png" alt=""> 银行卡支付</span><span><img src="../../static/img/noxuan.png" alt="" @click="weixinPay($event)"></span></div>
               </div>    
        </div>
        <div class="footer">
              <div><span>合计￥688&nbsp&nbsp&nbsp&nbsp</span><span>确认付款</span></div>
        </div>
      </div>  
  </div>
</template>
<script>
import $ from 'jquery'
export default {
  name: 'payment',
  data () {
    return {
        paySchool:""
    }
  },
  methods:{
    weixinPay(even){
      if (!$(event.target).attr("checked")) {
        console.log("选中")
        var xuanImg = require("../../static/img/xuan.png");
        $(event.target).attr("checked",true);
        $(event.target).attr("src",xuanImg);
      }else{
        console.log("取消");
        var noxuanImg = require("../../static/img/noxuan.png");
        $(event.target).attr("checked",false);
        $(event.target).attr("src",noxuanImg);
      }
    }
  },
  created:function(){
    this.paySchool = this.$route.query.topay;
  },
  mounted:function(){
    $("footer").hide();
  },
 destroyed: function () {
    $("footer").show();
 }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!-- scoped说明样式只能在当前组件用 -->
<style scoped>
 .order {  
     height: 1.04941334rem;  
     line-height: 1.04941334rem; 
     text-align: center;  
  }  
   .order .line {  
     display: inline-block;  
     width: 14.7%;  
     border-top: 1px solid #7B7D5C; ;  
  }  
  .order .txt {  
     vertical-align: middle;  
     font-size: 0.4rem;
     font-family: PingFangSC-Medium;
    color: #7B7D5C;
  }
  .saleNum,.pay{
    /*height: 3.76rem;*/
    background: #fff;
    margin-bottom: 0.2668rem;
  }  
  .saleNum>div,.pay>div{
    height: 1.2533333rem;
  }
  .saleNum>div>span,.manNum>div>span,.pay>div>span{
    display: inline-block;
    font-family: PingFangSC-Regular;
    font-size: 0.32rem;
    color: #613F05;
    line-height: 1.2533335rem;
  }
  .saleNum>div>span:nth-child(1),.manNum>div>span:nth-child(1),.pay>div>span:nth-child(1){
    width: 60%;
    text-align: left;
  }
  .saleNum>div>span:nth-child(2),.manNum>div>span:nth-child(2),.pay>div>span:nth-child(2){
    width: 33%;
    text-align: right;
  }
  .pay>div>span:nth-child(1)>img{
    width: 17%;
  }
  .pay>div>span:nth-child(2)>img{
    width: 10%;
  }
  /*flex最外框*/
  .page {
    display: flex;
    flex-direction: column;/* 该框从顶部列出其内容。 */
    height: 100%;
  }
/*滚动条不显示*/
  .content::-webkit-scrollbar {
    display:none
  }
  /*flex内容区*/
  .page .content {
    position: relative;
    flex: 1;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    background-color: #f4f4f4;
  }
  .footer {
    overflow: hidden;  
  }
  .footer>div{
    height: 1.30666667rem;
  }
  .footer>div>span{
    line-height: 1.30666667rem;
    display: inline-block;
    font-family: PingFangSC-Regular;
    font-size: 0.4rem;
  }
  .footer>div>span:nth-child(1){
    width: 60.3%;
    text-align: right;
    color: #fff;
    background: #2C2C2C;
  }
  .footer>div>span:nth-child(2){
    width: 39.7%;
    text-align: center;
    color: #613F05;
    background: #EFDCBB;
  }

</style>



